.App {
  background-color: #308391;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
}

df-messenger {
  --df-messenger-bot-message: #d9d9d9;
  --df-messenger-button-titlebar-color: #2b303e;
  --df-messenger-chat-background-color: #fafafa;
  --df-messenger-font-color: #434343;
  --df-messenger-send-icon: #878fac;
  --df-messenger-user-message: #a5d175;
  --df-messenger-button-titlebar-font-color: #fff;
}

#minimizeIcon{
  visibility: unset !important;
}

.df-chat-icon .default .show {
  /* background-color: red !important; */
  width: 200px;
